const initialState = {
    loading: true,
    error: false,
    items: [],
    catalogPage: 1,
    selectedCategory: '',
    categories: [],
    price: {
        from: '', to: ''
    },
    item: null,
    // Часом треба відререндерити клас, і тоді воно пригодиться
    rerender: true,
    total: 0,
    sliders: [],
    // Оскільки на одній сторінці рендериться 2 компоненти, що використовують items, для уникнення конфлікту використаємо нову змінну
    mainPageCategories: [],
    menuCategories: [],
    cartProducts: []
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CATALOG_MENU_REQUESTED':
            return {
                ...state, loading: true,
            };
        case 'CATALOG_MENU_LOADED':
            return {
                ...state, loading: false, items: [...state.items, ...action.payload], catalogPage: state.catalogPage + 1
            }
        case 'CATEGORY_SELECTED':
            return {
                ...state, selectedCategory: action.payload,
                catalogPage: 1, items: [], loading: true
            }
        case 'CATEGORY_REQUESTED':
            return {
                ...state, loading: true
            };
        case 'CATEGORIES_LOADED':
            return {
                ...state, categories: action.payload, loading: false
            };
        case 'CATALOG_FILTER_PRICE':
            return {
                ...state, items: [], loading: true, price: action.payload
            };
        case 'MENU_SELECTED':
            return {
                ...state, loading: true, price: initialState.price, catalogPage: initialState.catalogPage, selectedCategory: '', items: []
            };
        case 'PRODUCT_DETAILS_LOADING':
            return {
                ...state, loading: true
            };
        case 'PRODUCT_DETAILS_LOADED':
            return {
                ...state, loading: false, item: action.payload
            };
        case 'PRODUCT_ADDED_TO_CART':
            return {
                ...state, rerender: !state.rerender
            };
        case "CART_PRODUCTS_REQUESTED":
            return {
                ...state, loading: true
            };
        case 'CART_PRODUCTS_LOADED':
            return {
                ...state, loading: false, cartProducts: action.payload.items, total: action.payload.total,
            };
        case 'CLEAR_ITEMS':
            return {
                ...state, items: [], loading: true, catalogPage: 1
            };
        case 'WISHLIST_REQUESTED':
            return {
                ...state, loading: true
            };
        case 'WISHLIST_LOADED':
            return {
                ...state, loading: false, items: action.payload
            };

        case 'WISHLIST_ADDED':
            return {
                ...state, rerender: !state.rerender, loading: false,
            }
        case 'WISHLIST_EMPTY':
            return {
                ...state, items: [], rerender: !state.rerender, loading: false
            };
        case 'SET_TOTAL_VALUE':
            return {
                ...state, total: action.payload
            };
        case 'SLIDERS_REQUESTED':
            return {
                ...state, loading: true
            };
        case 'SLIDERS_LOADED':
            return {
                ...state, sliders: action.payload, loading: false
            };

        case 'MAIN_PAGE_PRODUCTS_REQUESTED':
            return {
                ...state, loading: true
            };
        case 'MAIN_PAGE_PRODUCTS_LOADED':

            return {
                ...state, loading: false, items: action.payload
            }

        case 'MAIN_PAGE_CATEGORIES_REQUESTED':
            return {
                ...state, loading: true
            };

        case 'MAIN_PAGE_CATEGORIES_LOADED':
            return {
                ...state, loading: false, mainPageCategories: action.payload
            };

        case 'MENU_CATEGORIES_LOADED':
            return {
                ...state, menuCategories: action.payload
            };
        default:
            return state;
    }
};

export default reducer;