import React, {Component} from 'react';

import SearchImg from './search.png';
import HeartImg from './heart.png';
import CartImg from './cart.png';
import LogoImg from '../logo.png';

import {Link} from "react-router-dom";

import {connect} from 'react-redux';

import {menuCategoriesLoaded, menuSelected, setTotalValue, clearItems} from "../../../actions/actions";

import WithRequestManager from "../../withRequestManager";

class Menu extends Component {
    async componentDidUpdate(prevProps, prevState, snapshot) {
        const cart = JSON.parse(localStorage.getItem('cart'));

        let sum = 0;

        for (const id of cart) {
            const product = await this.props.Request.getProduct(id);

            sum += product.price;
        }


        this.props.setTotalValue(sum);
    }

    async componentDidMount() {
        const cart = JSON.parse(localStorage.getItem('cart'));

        let sum = 0;

        if (cart) {

            for (const id of cart) {
                const product = await this.props.Request.getProduct(id);

                sum += product.price;
            }

            this.props.setTotalValue(sum);
        }

        this.props.Request.getCategories('').then(json => this.props.menuCategoriesLoaded(json));

    }


    render() {
        const {total, menuCategories} = this.props;
        const cart = JSON.parse(localStorage.getItem('cart'));

        const cartCount = cart ? cart.length : 0;


        const RenderCartButtons = () => {

            return (
                <>
                    <Link to='/cart'>
                        <img
                            src={CartImg}
                            alt="Корзина"/>
                        <span>{cartCount}</span>
                    </Link>

                    <div className="price">₴{total}</div>
                </>
            );


        };

        const currentRoute = window.location.pathname.split('/')[1];


        return (
            <>
                {/* Потрібно для нормальної роботи */}
                <div className="offcanvas-menu-overlay"/>

                <div className="offcanvas-menu-wrapper">

                    <div className="offcanvas__nav__option">

                        <a href="#" className="search-switch">
                            <img src={SearchImg} alt="Пошук"/>
                        </a>

                        <a href="#">
                            <img src={HeartImg} alt="Список бажань"/>
                        </a>

                        <a href="#"><img src={CartImg} alt="Кошик"/> <span>{cartCount}</span></a>

                        <div className="price">₴{total}</div>
                    </div>


                    <div id="mobile-menu-wrap"/>

                </div>

                <header className="header" onClick={this.props.menuSelected}>
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3 col-md-3">

                                <div className="header__logo">
                                    <a href="/"><img
                                        src={LogoImg}
                                        alt="На головну"/>
                                    </a>
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6">
                                <nav className="header__menu mobile-menu">
                                    <ul>
                                        <li className={currentRoute === '' ? 'active' : ''}>
                                            <Link to='/'>Домашня</Link>
                                        </li>
                                        <li className={currentRoute === 'catalog' ? 'active' : ''}>
                                            <Link to='/catalog'>Каталог</Link>

                                            {
                                                (() => {
                                                    if (menuCategories.length > 0) {
                                                        return (
                                                            <ul className="dropdown">

                                                                {
                                                                    menuCategories.map(category => {
                                                                        return (
                                                                            <>
                                                                                <li>
                                                                                    <Link
                                                                                        to={'/catalog/' + category.alias}>{category.title}</Link>
                                                                                </li>
                                                                            </>
                                                                        );
                                                                    })
                                                                }

                                                            </ul>
                                                        );
                                                    }
                                                })()
                                            }
                                        </li>
                                        <li><a href="#">Про нас</a></li>
                                    </ul>
                                </nav>
                            </div>
                            <div className="col-lg-3 col-md-3">
                                <div className="header__nav__option">

                                    <a href="#" className="search-switch"><img src={SearchImg} alt="Пошук"/></a>

                                    <Link to='/wishlist'><img
                                        src={HeartImg}
                                        alt="Список бажань"/>
                                    </Link>

                                    <RenderCartButtons/>

                                </div>
                            </div>
                        </div>
                        <div className="canvas__open"><i className="fa fa-bars"/></div>
                    </div>
                </header>

            </>
        );
    }
}


const mapStateToProps = (state) => {
    return {
        total: state.total,
        rerender: state.rerender,
        loading: state.loading,
        item: state.item,
        sliders: state.sliders,
        menuCategories: state.menuCategories
    };
};

export default WithRequestManager()(connect(mapStateToProps, {
    menuSelected,
    setTotalValue,
    menuCategoriesLoaded,
    clearItems
})(Menu));